import { useEffect } from "react"

export const Services = () => {

    useEffect(() => {
        const modalViews = document.querySelectorAll(".services__modal"),
            modalBtns = document.querySelectorAll(".services__button"),
            modalClose = document.querySelectorAll(".services__modal-close");
        let modal = function (modalClick) {
            modalViews[modalClick].classList.add("active-modal");
        }
        modalBtns.forEach((modalBtn, i) => {
            modalBtn.addEventListener("click", () => {
                modal(i)
            })
        })
        modalClose.forEach(modalClose => {
            modalClose.addEventListener("click", () => {
                modalViews.forEach(modalView => {
                    modalView.classList.remove("active-modal");
                });
            });
        })
    }, []);
    return (
        <section className="services section" id="services">
            <h2 className="section__title">Servicios</h2>
            <span className="section__subtitle">Mis servicios</span>

            <div className="services__container container grid">
                <div className="services__content">
                    <div>
                        <i className="uil uil-server services__icon"></i>
                        <h3 className="services__title">Backend</h3>
                    </div>
                    <span className="button button--flex button--small button--link services__button">
                        Ver ms
                        <i className="uil uil-arrow-right button__icon"></i>
                    </span>
                    <div className="services__modal">
                        <div className="services__modal-content">
                            <h4 className="services__modal-title">Backend </h4>
                            <i className="uil uil-times services__modal-close"></i>
                            <ul className="services__modal-services grid">
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Desarrollo e implementacin de aplicaciones web.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Despliegue de aplicaciones con dominio y certificado SSL en la nube.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Arquitectura MVC o de micro servicios.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Implementacin de Docker.</p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>

                <div className="services__content">
                    <div>
                        <i className="uil uil uil-arrow services__icon"></i>
                        <h3 className="services__title">Frontend </h3>
                    </div>
                    <span className="button button--flex button--small button--link services__button">
                        Ver ms
                        <i className="uil uil-arrow-right button__icon"></i>
                    </span>
                    <div className="services__modal">
                        <div className="services__modal-content">
                            <h4 className="services__modal-title">Frontend </h4>
                            <i className="uil uil-times services__modal-close"></i>
                            <ul className="services__modal-services grid">
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Maquetado web adaptable a moviles con HTML, CSS y Javascript.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Implementacin de Bootstrap.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Animaciones a elementos web.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Creacin aplicaciones de todo tipo.</p>
                                </li>

                            </ul>
                        </div>
                    </div>
                </div>

                <div className="services__content">
                    <div>
                        <i className="uil uil-database services__icon"></i>
                        <h3 className="services__title">Databases</h3>
                    </div>
                    <span className="button button--flex button--small button--link services__button">
                        Ver ms
                        <i className="uil uil-arrow-right button__icon"></i>
                    </span>
                    <div className="services__modal">
                        <div className="services__modal-content">
                            <h4 className="services__modal-title">Databases</h4>
                            <i className="uil uil-times services__modal-close"></i>
                            <ul className="services__modal-services grid">
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Diseo de bases de datos para aplicaciones de todo tipo.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Implementacin de bases de datos con SQL.</p>
                                </li>
                                <li className="services__modal-service">
                                    <i className="uil uil-check-circle services__modal-icon"></i>
                                    <p>Bases de datos relacionales o no relacionales.</p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}
